import AuthInput, { AuthContainer } from "../components/common/Auth";
import { AuthButton, AuthLinkText, AuthTitle } from "../components/common/Auth";
import { ReactComponent as Logo } from "../assets/icons/LOGO.svg";
import { Link, useNavigate } from "react-router-dom";

import { useState } from "react";
import { register } from "api/auth";
import Swal from "sweetalert2";

const LoginPage = () => {
  const [account, setAccount] = useState("");
  const [name, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");

  const nameInputLength = name.length;

  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.length === 0) {
      return;
    }
    if (name.length === 0) {
      return;
    }
    if (email.length === 0) {
      return;
    }
    if (password.length === 0) {
      return;
    }
    if (checkPassword.length === 0) {
      return;
    }
    const {
      status,
      data: { token: authToken },
    } = await register({ name, account, email, password, checkPassword });

    if (status === "success") {
      localStorage.setItem("authToken", authToken);

      // 
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      navigate("/");
      return;
    }

    // 
    Swal.fire({
      position: "top",
      title: "",
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
  };

  return (
    <AuthContainer>
      <Logo />
      <AuthTitle></AuthTitle>
      <AuthInput
        label=""
        placeholder=""
        alertText="account "
        value={account}
        onChange={(accountInputValue) => setAccount(accountInputValue)}
      />
      <AuthInput
        label=""
        placeholder=""
        alertText={nameInputLength > 50 ? "" : ""}
        wordCount={`${nameInputLength}/50`}
        value={name}
        onChange={(nameInputValue) => setUsername(nameInputValue)}
      />
      <AuthInput
        label="Email"
        placeholder=" Email"
        alertText="email "
        value={email}
        onChange={(emailInputValue) => setEmail(emailInputValue)}
      />

      <AuthInput
        type="password"
        label=""
        placeholder=""
        value={password}
        onChange={(passwordInputValue) => setPassword(passwordInputValue)}
      />
      <AuthInput
        type="password"
        label=""
        placeholder=""
        alertText=""
        value={checkPassword}
        onChange={(checkPasswordInputValue) =>
          setCheckPassword(checkPasswordInputValue)
        }
      />
      <AuthButton onClick={handleClick}></AuthButton>
      <Link to="/users/login">
        <AuthLinkText></AuthLinkText>
      </Link>
      
    </AuthContainer>
  );
};

export default LoginPage;
