import React from 'react'
import './setting.scss'

const dummyUserInfo = {
    id: 1,
    account: 'wonderwoman',
    name: 'Diana',
    email: 'diana@gmail.com',
    password: '',
    passwordComfirmation: ''
}

const Setting = () => {
  return (
    <div className="settingContainer">
        <div className="settingHeader"></div>
        <div className="settingForm">
            <form>
                <label>
                    
                    <input type="text" defaultValue={dummyUserInfo.account} placeholder="" />
                </label>
                <label>
                    
                    <input type="text" defaultValue={dummyUserInfo.name} placeholder="" />
                </label>
                <label>
                    Email
                    <input type="text" defaultValue={dummyUserInfo.email} placeholder="Email" />
                </label>
                <label>
                    
                    <input type="text" placeholder="" />
                </label>
                <label>
                    
                    <input type="text" placeholder="" />
                </label>
            </form>
        </div>
        <div className="saveBtn">
           <button type="submit"></button>
        </div>
    </div>
  )
}

export default Setting